<script>
import LineChartComponent from '@/components/graphic/LineChart.vue';
import { ref, onMounted } from "vue";

export default {
  name: 'StatisticPage',
  components: {
    LineChartComponent
  },
  setup() {
    const typeClient = ref('');

    onMounted(() => {
      const originType = localStorage.getItem('origin_type');
      if (originType) {
        typeClient.value = originType;
      }
    });

    return {
      typeClient
    };
  }
};
</script>

